.theme_color_project-default {
  --default-block-color: #ffffff;
  --accent-block-color: #ffcc00;
  --default-border-color: #000000;

  --main-text-color: #000000;
  --second-text-color: rgba(0, 0, 0, 0.6);
  --additional-text-color: rgba(0, 0, 0, 0.3);
  --link-color: #0077ff;
}

.theme_color_project-brand {
  --default-block-color: #0077ff;
  --accent-block-color: #ffffff;
  --default-border-color: #ffffff;

  --main-text-color: #ffffff;
  --second-text-color: rgba(255, 255, 255, 0.6);
  --additional-text-color: rgba(255, 255, 255, 0.3);
  --link-color: #ffffff;
}

.theme_color_project-inverse {
  --default-block-color: #000000;
  --accent-block-color: #ffffff;
  --border-block-color: #ffffff;

  --main-text-color: #ffffff;
  --second-text-color: rgba(255, 255, 255, 0.6);
  --additional-text-color: rgba(255, 255, 255, 0.3);
  --link-color: #0077ff;
}

.theme_color_project-warning {
  --default-block-color: #ff3333;
  --accent-block-color: #ffffff;
  --border-block-color: #ff3333;

  --main-text-color: #ffffff;
  --second-text-color: rgba(255, 255, 255, 0.6);
  --additional-text-color: rgba(255, 255, 255, 0.3);
  --link-color: #ffffff;
}

.theme_size_default {
  --text-size-s: 8px;
  --text-size-m: 10px;
  --text-size-l: 11px;
  --text-size-xl: 13px;
  --text-size-xxl: 16px;

  --line-height-s: 21px;
  --line-height-m: 25px;
  --line-height-l: 28px;
  --line-height-xl: 34px;
  --line-height-xxl: 38px;
}

.theme_space_default {
  --space-xxxs: 3px;
  --space-xxs: 6px;
  --space-xs: 9px;
  --space-s: 15px;
  --space-m: 18px;
  --space-l: 24px;
  --space-xl: 27px;
  --space-xxl: 36px;
  --space-xxxl: 45px;
  --space-xxxxl: 51px;
  --space-xxxxxl: 72px;
}

.theme_gap_small {
  --gap: var(--space-s);
  --gap-half: var(--gap)/2;

  @media (min-width: $desktop-resolution) {
    --gap: var(--space-xl);
  }
}